html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/materialicons/v41/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format('woff2');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}
#root {
  height: 100%; }

.App {
  position: relative;
  min-height: 100%; }
  .App-header {
    margin-top: 30px;
    text-align: center; }
    .App-header__nav {
      margin-bottom: 30px; }
    .App-header__bubble {
      -webkit-transition-property: all !important;
      transition-property: all !important; }
  .App-scene {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1; }
    .App-scene.expanded {
      width: 100vw;
      min-height: 100vh; }
  .App-contact-form {
    text-align: start; }
    .App-contact-form > div {
      margin-bottom: 15px; }
    .App-contact-form button {
      margin: 5px; }
  .App-map {
    height: 400px; }
    .App-map .leaflet-container {
      height: 400px;
      width: 100%; }
    .App-map .leaflet-popup-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center; }
      .App-map .leaflet-popup-content p {
        margin-top: 10px; }
  .App-home__actions {
    margin-top: 50px; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.u-flex {
  display: flex; }
  .u-flex > * {
    flex: 1 1; }

